<template>
    <div class="search">
        <van-search
          v-model="value"
          show-action
          placeholder="请搜索商店,店铺"
        >
          <template #action>
            <van-icon name="apps-o" />
          </template>
          <template #left>
            <van-icon name="arrow-left" @click="back" />
          </template>
        </van-search>
        <div class="boxList" v-for="(item,index) in listData" :key="index" @click="goDetail(item.goods_id)">
            <div class="fl listImg">
                <img :src="item.goods_big_logo" />
            </div>
            <div class="listContent fl">
                <ul>
                    <li>{{item.goods_name}}</li>
                    <li>￥{{item.goods_price}}</li>
                </ul>
            </div>
        </div>
        <van-empty
          v-if="empty"
          class="custom-image"
          image="https://img.yzcdn.cn/vant/custom-empty-image.png"
          description="暂时未搜索到相关物品，请返回搜索~"
        />
    </div>
</template>

<script>
    import "../assets/css/search.less";
    export default {
        data(){
            return{
                value:this.$route.query.query,
                listData:[],
                empty:false
            }
        },
        created(){
            this.query = this.$route.query.query
            this.Lists(this.query);
        },
        methods:{
            Lists(query){
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: 'GET',
                    url: '/goods/search',
                    params: {
                        query,
                    }
                }).then(result => {
                    this.$toast.clear();
                    if (result.data.meta.status == 200) {
                        if(result.data.message.goods.length == 0){
                            this.empty = true;
                        }
                        
                        this.listData = result.data.message.goods;    
                    }
                }).catch(err => {
                    this.$toast.clear();       
                })
            },
            back() {
                this.$router.go(-1);
            },
            goDetail(goods_id) {
                this.$router.push({name: 'Detail', query: {goods_id}});
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>